
<template>
    <div data-view="food-list" class="ke-fs-content-view scroll" :class="{'show-goods-img' : !!showGoodsImg}">
        <div v-if="showGoodsImg" class="ke-fs-food-list-popup" @click="_hideGoodsImg">
            <div class="ke-fs-food-popup-inner">
                <div class="ke-fs-food-big-cover">
                    <van-icon name="close" @click="_hideGoodsImg" />
                    <img :src="showGoodsImg" />
                </div>
            </div>
        </div>
        <van-swipe class="ke-fs-food-list-swipe" :autoplay="3000" :show-indicators="false">
            <van-swipe-item v-for="(image, index) in banners" :key="index" @click="isShowTips=true">
                <img :src="image" />
            </van-swipe-item>
        </van-swipe>
        <van-overlay :show="isShowTips" :z-index="1024" @click="isShowTips=false">
            <div class="ke-fs-food-list-tips">
                <h6>温馨提示:</h6>
                <ol>
                    <li>目前中心简餐包括商务简餐、特色简餐、面食三种午餐类型。</li>
                    <li>每日每人仅可选择上述简餐类型中一种。</li>
                    <li>实行提前订餐制（即提前一天对次日午餐下单），订餐截止时间为订餐当日下午14:00。超过订餐时间，将无法订餐（修改套餐除外），请提前做好订餐。</li>
                    <li>简餐5人以上起订，若少于5人（含5人）预订，则订餐失败，系统将在订餐当日下午16:00前推送订餐失败消息，请及时查收并在1小时内完成订单修改。</li>
                    <li>就餐时，凭系统生成的订单号到中心食堂取餐。</li>
                </ol>
                <van-button type="danger" size="small" block>关 闭</van-button>
            </div>
        </van-overlay>
        <van-pull-refresh v-model="isRefresh" @refresh="_onRefresh">
            <ul class="ke-fs-food-categories">
                <li v-for="(c) in categories" 
                    :key="c.type" 
                    :class="{'actived' : c.type === selectedCategory}"
                    @click="_setCategory(c.type)">
                    <img :src="c.type === selectedCategory ? c.icon.actived : c.icon.normal"  alt="c.label"/>
                    <span v-html="c.label"></span>
                </li>
            </ul>
            <div class="ke-fs-food-list">
                <template v-if="isInit">
                    <van-list v-model="isLoading" :finished="isFinished" finished-text="" :offset="40">
                        <div v-for="combo in curComboList" :key="combo.id" class="ke-fs-food-item">
                            <img 
                                :src="combo.comboImg && combo.comboImg.length > 0 ? `./common/download?fileName=${combo.comboImg}` : cover"
                                @click="_showGoodsImg(combo.comboImg)" />
                            <div class="ke-fs-food-item__summary">
                                <h4>{{combo.comboName}}</h4>
                                <ul v-if="1 == combo.comboType && combo.goods.length > 0">
                                    <li v-for="g in combo.goods" :key="g.id">{{g.goodsName}}</li>
                                </ul>
                                <p>
                                    <span>已有<strong>{{combo.cnt}}</strong>人点餐</span>
                                </p>
                                <van-button type="danger" icon="plus" size="mini" @click="_confrim(combo.id)" round :disabled="-1 != status && !isMod">点餐</van-button>
                            </div>
                        </div>
                        <van-empty v-if="isFinished && curComboList.length == 0" :image="empty" description="今日菜品还未上架..." />
                    </van-list>
                </template>
                <div v-else class="ke-fs-food-list__loading">
                    <van-loading type="spinner">加载中,请稍后...</van-loading>
                </div>
            </div>
        </van-pull-refresh>
    </div>
</template>

<script>
    import { queryComboList } from '@/api/combo-api';
    import { createOrder, modifyOrder } from '@/api/order-api';
    import { Dialog } from 'vant';

    const COMBO_TYPE_NAMES = {
        type1 : 'jc',
        type2 : 'ms',
        type3 : 'ts'
    };

    export default {
        data() {
            return {
                isInit           : false,
                isMod            : false,
                isRefresh        : false,
                isLoading        : false,
                isFinished       : false,
                isShowTips       : false,
                selectedCategory : COMBO_TYPE_NAMES.type1,
                categories       : [{
                    type  : COMBO_TYPE_NAMES.type1,
                    label : '商务<br />简餐',
                    icon : {
                        normal  : require('../../assets/img/icon/icon_tc.png'),
                        actived : require('../../assets/img/icon/icon_tc_hover.png'),
                    }
                },{
                    type  : COMBO_TYPE_NAMES.type3,
                    label : '特色<br />美食',
                    icon  : {
                        normal  : require('../../assets/img/icon/icon_ts.png'),
                        actived : require('../../assets/img/icon/icon_ts_hover.png'),
                    }
                },{
                    type  : COMBO_TYPE_NAMES.type2,
                    label : '精美<br />面食',
                    icon : {
                        normal  : require('../../assets/img/icon/icon_ms.png'),
                        actived : require('../../assets/img/icon/icon_ms_hover.png')
                    }
                }],
                comboObjs  : null,
                status     : 0,
                oldOrderId : null,
                showGoodsImg : null,
                cover : require('../../assets/img/kfc.jpg'),
                empty : require('../../assets/img/empty.png'),
                banners : [
                    require('../../assets/img/banner.png'),
                    require('../../assets/img/banner_2.png')
                ]
                
            }
        },
        computed : {
            curComboList() {
                return this.comboObjs ? this.comboObjs[this.selectedCategory] || [] : [];
            }
        },
        methods : {
            _showGoodsImg(img = '') {
                if(img.trim().length > 0) {
                    this.showGoodsImg = `./common/download?fileName=${img}`;
                }
            },

            _hideGoodsImg() {
                this.showGoodsImg = null;
            },

            _onRefresh() {
                setTimeout(() => {
                    this.isRefresh = false;
                }, 2000);
            },

            _setCategory(category) {
                this.selectedCategory = category;
            },

            _queryAllComboList() {
                this.isLoading = true ;
                this.isFinished = false;
                queryComboList()
                    .then(({ list, status }) => {
                        this.comboObjs = list.reduce((obj, c) => {
                            const name = COMBO_TYPE_NAMES[`type${c.comboType}`];
                            if(!obj[name]) {
                                obj[name] = [];
                            }
                            obj[name].push(c);
                            return obj;
                        }, {});
                        this.isInit = true;
                        this.status = Number(status);
                        this.isLoading = false;
                        this.isFinished = true;
                    })
                    .catch((/*err*/) => {
                        this.isInit = true;
                        this.isLoading = false;
                        this.isFinished = true;
                        // console.error(err);
                    });
            },
            _confrim(comboId) {
                const $loading = this.$toast.loading({
                    message     : '提交中...',
                    forbidClick : true
                });

                let fn, params;
                if(this.isMod) {
                    fn = modifyOrder;
                    params = [this.oldOrderId, comboId];
                } else {
                    fn = createOrder;
                    params = [comboId];
                }

                fn.apply(null, params)
                    .then(() => {
                        $loading.clear();     
                        Dialog.confirm({
                            title   : '系统提示',
                            message : '点餐成功,是否立即查看您的订单?'
                        })
                        .then(()=> this.$router.push('/myOrder'))
                        .catch(()=> /*console.log('cancel')*/{});
                    })
                    .catch(err => {
                        $loading.clear();
                        Dialog.alert({
                            message : `点餐失败,原因:${err}`,
                            theme   : 'round-button'
                        });
                    });
            },
            _checkRouter() {
                const route = this.$router.currentRoute;
                if('true' === route.query.isMod) {
                    const oid = route.query.oid || '';
                    if(oid.trim().length > 0) {
                        this.isMod = true;
                        this.oldOrderId = Number(oid);
                    }
                }
            }
        },
        watch : {
            '$router' : function() {
                this._checkRouter();
            } 
        },
        created() {
            this._checkRouter();
            // console.log(this.isMod, this.oldOrderId, this.status);
            // console.log(0 == this.status);
            this._queryAllComboList();
        }
    }
</script>